// /app/oauth/redirect/page.js
// URL에서 토큰을 추출하고 저장한 뒤, 메인 페이지로 이동시키는 역할

"use client"

import { useEffect, useState } from "react"
import { useRouter, useSearchParams } from "next/navigation"
import { Loader2, AlertCircle } from "lucide-react"
import Link from "next/link"
import { Button } from "@/components/ui/button"

export default function OAuthRedirectPage() {
  const router = useRouter()
  const searchParams = useSearchParams()
  const [message, setMessage] = useState("로그인 처리 중입니다. 잠시만 기다려주세요...")
  const [isError, setIsError] = useState(false)

  useEffect(() => {
    // 백엔드에서 쿼리 파라미터로 전달한 토큰을 추출
    const accessToken = searchParams.get("accessToken")
    const refreshToken = searchParams.get("refreshToken")
    const error = searchParams.get("error")

    if (error) {
      console.error("OAuth Login Error:", error)
      setMessage(`로그인 실패: ${error}.`)
      setIsError(true)
      // 에러 발생 시 3초 후 로그인 페이지로 이동
      setTimeout(() => router.push("/login"), 3000)
      return
    }

    if (accessToken && refreshToken) {
      // 1. 토큰을 localStorage에 저장 (실제 환경에서는 보안을 위해 HttpOnly 쿠키 권장)
      localStorage.setItem("accessToken", accessToken)
      localStorage.setItem("refreshToken", refreshToken)

      // 2. 메시지 변경 후 메인 페이지로 이동
      setMessage("로그인 성공! 메인 페이지로 이동합니다.")
      console.log("로그인 성공! 메인 페이지로 이동합니다.")
      router.push("/")
      
    } else {
      // 토큰 정보가 누락된 경우
      setMessage("토큰 정보가 누락되었습니다. 다시 시도해 주세요.")
      setIsError(true)
      console.warn("리다이렉트되었으나 토큰 정보가 누락되었습니다.")
      // 3초 후 로그인 페이지로 이동
      setTimeout(() => router.push("/login"), 3000)
    }
  }, [searchParams, router])

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 p-4">
      <div className="w-full max-w-md p-8 bg-white rounded-xl shadow-lg space-y-6 text-center">
        {isError ? (
          <AlertCircle className="h-10 w-10 text-red-500 mx-auto" />
        ) : (
          <Loader2 className="h-10 w-10 animate-spin text-primary mx-auto" />
        )}
        
        <h1 className="text-xl font-semibold text-gray-700">{message}</h1>
        
        {isError && (
          <Button asChild variant="outline" className="w-full">
            <Link href="/login">
              로그인 페이지로 돌아가기
            </Link>
          </Button>
        )}
      </div>
    </div>
  )
}